
const url = process.env.VUE_APP_API_HOST;  //.env//8199 keypir//8200 
//webSocket
let Socket:WebSocket | null
let setIntervalWesocketPush:number
//websocket
//
const onopenWs = ()=>{
    console.log("")
}
//
const onerrorWs = ()=>{
    Socket?.close()
    clearInterval(setIntervalWesocketPush)
    if(Socket?.readyState !== 3){ //stateCode 3 
        Socket = null
        createSocket()
        console.log("")
    }
}
//
const oncloseWs = () => {
    clearInterval(setIntervalWesocketPush)
    alert('websocket....')
    if (Socket?.readyState !== 2) {  //readyState 2 = 
      Socket = null
      createSocket()
    }
  }

/**
 * 
 * @param {any} message 
 */
 const connecting = (message:any) => {
    setTimeout(() => {
      if (Socket?.readyState === 0) { //readyState 0 connecting
        connecting(message)
      } else {
        Socket?.send(message)
        
      }
    }, 1000)
  }
//
/**
 * 
 * @param {any} message 
 */
 export const sendWSPush = (message:any) => { //lookupTypeproto
    if (Socket !== null && Socket.readyState === 3) {
      Socket.close()
      createSocket()
    } else if (Socket?.readyState === 1) { //
      Socket.send(message)
    } else if (Socket?.readyState === 0) { //
      connecting(message)
      console.log("readyState:"+Socket.readyState,":"+message);
    }
  }

  export const onmessageWs=(msg:any)=>{
    if(msg){ 
        //onmessageWsheaderVuex
      window.dispatchEvent(new CustomEvent('onmessageWs',{
          detail:{
            msg
          }
        }))
    }
}
  /**
 * @param {number} time  5000
 * @param {string} ping  ping
 */
export const sendPing = (time = 5500, ping = 'ping') => {
    clearInterval(setIntervalWesocketPush)
    Socket?.send(ping)
    setIntervalWesocketPush = setInterval(() => {
      Socket?.send(ping)
    }, time)
  }
  //ws
export const createSocket =()=>{  //createSocket
    console.log("")
    Socket && Socket.close()
    if(!Socket){
        Socket = new WebSocket(url);
        Socket.binaryType='arraybuffer' //binaryType
        Socket.onopen = onopenWs  //
        Socket.onmessage = onmessageWs    
        Socket.onerror = onerrorWs
        Socket.onclose = oncloseWs
        console.log("websocket");
    }
}



