
export const state = {
  LobbyInfo: {
  },
  tableAUid:"",
  tableBuid:"",
};
export const actions = {}

export const mutations = {
  LobbyInfo(state:any, payload:any) { //wbSocket
    state.LobbyInfo=payload
    // console.log("vuex-lobby",state.LobbyInfo)
    state.tableAUid = payload.tables[0].uuid
    state.tableBuid = payload.tables[1].uuid
  },
};
export const getters = {}

export default {
  state,
  actions,
  mutations,
  getters,
  namespaced: true,
};